import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, AbstractControl, FormControl } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators';
import 'rxjs/add/observable/of';
/**
 * Generated class for the PayementPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-payement',
  templateUrl: 'payement.html',
})
export class PayementPage {

  formgroup:FormGroup;
  firstname:AbstractControl;
  lastname:AbstractControl;
  age:AbstractControl;
  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder, public loadingCtrl: LoadingController,
    private alertCtrl: AlertController) {

    this.formgroup = formBuilder.group({
      firstname:['',Validators.compose([
        Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern('[a-zA-Z ]*')
      ]),
      this.validPassword.bind(this)
     ],
     lastname:['',Validators.compose([
      Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern('[a-zA-Z ]*')
    ]),
    this.validPassword.bind(this)
   ],
   age:['',Validators.compose([
    Validators.required, Validators.minLength(2), Validators.maxLength(2), 
  ]),
  this.validPassword2.bind(this)
 ],
    });

   // this.firstname = new FormControl('Dayana', Validators.required)
   // this.lastname = new FormControl('Dayana', Validators.required)
   // this.age = new FormControl('Dayana', Validators.required)

    this.firstname = this.formgroup.controls['firstname'];
    this.lastname = this.formgroup.controls['lastname'];
    this.age = this.formgroup.controls['age']; 
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad PayementPage');
  }
  validPassword(control: AbstractControl) {
    return Observable.of('anbcvcb' === control.value).pipe(
      map(result => result ? { invalid: true } : null)
    );
  }
  validPassword2(control: AbstractControl) {
    return Observable.of('12' === control.value).pipe(
      map(result => result ? { invalid: true } : null)
    );
  }

  onSubmit(value: any): void { 
    if(this.formgroup.valid) {
       // window.localStorage.setItem('username', value.username);
       // window.localStorage.setItem('password', value.password);
console.log(value);

let loading = this.loadingCtrl.create({
  content: 'Please wait...'
});

loading.present();

setTimeout(() => {
  loading.dismiss();
}, 5000);
this.inter();
    }

}

inter(){
  let a = setInterval(() =>{
     let alert = this.alertCtrl.create({
       title: 'impossible de se connecter au serveur',
       subTitle: 'si le probléme persiste contacter nous par mail.',
       buttons: [
         {
           text: 'Cancel',
           role: 'cancel',
           handler: () => {
             console.log('Cancel clicked');
             clearInterval(a)
           }
         },
       ]
     });
     alert.present();
   }, 10000);
 }

}



